
























import {Component, Vue} from 'vue-property-decorator';
import WalletRepository from '@/modules/wallet/repository/wallet-repository';
import {IFundHistory} from '@/modules/wallet/models/wallet';

@Component({})
export default class DepositWithdrawHistoryScreen extends Vue {
  headers = [
    {text: 'Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Type', align: 'center', sortable: false, value: 'seller'},
    {text: 'Amount', align: 'right', sortable: false, value: 'amount'},
    {text: 'Status', value: 'amount', align: 'center', sortable: false},
  ];

  loading: boolean = false;
  items: IFundHistory[] = [];

  async beforeMount() {
    this.loading = true;
    const wallet = await WalletRepository.getMyWallet();
    this.loading = false;

    if (wallet) {
      this.items = wallet.history.reverse();
    }
  }
}
